.App {
    text-align: center;
}

.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
}

.Container-multimedia {
    padding-bottom: 20px;
    padding-top: 20px;
}

.First-media-color {
    background-color: #e7dbce;
}

.Second-media-color {
    background-color: #d0c4ba;
}

.Third-media-color {
    background-color: #5d554e;
    color: white;
}

.Youtube-preview, .Youtube-dim{
    border:4px solid #5d554e;
    border-radius: 20px;
    position:relative;
    margin-left: auto;
    margin-right: auto;
    float:none;
    cursor: pointer;
}

.Youtube-dim {
    border:8px solid #000000;
    width: 426px; /*426 256*/
    height: 240px; /*240 144*/
    max-width: 426px; /*426 256*/
    max-height: 240px;
}


.Chat-col-background {
    background-color: #f5efe9;
}

.Vertical-fit {
    top: 50px;
    bottom: 28px;
    height: calc(100vh - 50px - 28px);
}

.Chatbot {
    top: 50px;
    bottom: 110px;
    height: calc(100vh - 50px - 110px);
}

.Scrollbar {
    overflow-y: scroll;
}

@-webkit-keyframes App-logo-spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@media only screen and (min-width : 481px) {
    .flex-row.row {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
    }
    .flex-row.row > [class*='col-'] {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: column;
            flex-direction: column;
    }
    .flex-row.row:after,
    .flex-row.row:before {
        display: -ms-flexbox;
        display: flex;
    }
}


